<template>
  <div id="app">

    <nav class="navbar is-fixed-top is-dark" role="navigation" aria-label="main navigation">
      <div id="navbarBasicExample" class="navbar-menu">
        <div class="navbar-start">
          <a class="navbar-item is-active">
            Movers and Laggers
          </a>
          <a class="navbar-item">
            Correlations
          </a>
          <a class="navbar-item">
            Exchange Analysis
          </a>
        </div>
      </div>
    </nav>

    <div>
      <button class="button"
        v-for="period in periods"
        @click="updateInterval(period)"
        :class="{'is-active': period == currentPeriod}">
        {{period}} MA
      </button>
    </div>
    <LaggersMoversTable :MA="MA" :laggers="laggers" :movers="movers"></LaggersMoversTable>
  </div>
</template>

<script>

import LaggersMoversTable from './components/LaggersMoversTable.vue'

export default {
  name: 'app',
  components: {
    LaggersMoversTable
  },
  data() {
    return {
      laggers: [],
      movers: [],
      MA: 'MA20',
      periods: [10, 20, 30, 50, 100, 200, 250],
      currentPeriod: 20
    }
  },
  methods: {
    loadLaggersMovers() {
      let url = 'http://198.35.45.239:1337/api/markets/relationsToMA/' + this.MA
      this.$http.get(url)
      .then(response => {
        console.log(response)
        this.laggers = response.body.below
        this.movers = response.body.above
      }, response => {
        console.log(response)
      })
    },
    updateInterval(interval) {
      this.MA = 'MA' + interval
      this.loadLaggersMovers()
      this.currentPeriod = interval
    }
  },
  mounted() {
    this.loadLaggersMovers()
  }
}
</script>

