<template>
  <div class="columns">
    <div class="column is-one-fifth">
    </div>
    <div class="column is-one-fifth">
      <h3>Laggers</h3>
      <ul>
        <li v-for="lagger in laggers">
          <a :href="binanceLink(lagger)">{{ lagger.baseAsset }} / {{ lagger.quoteAsset }}</a>
        </li>
      </ul>
    </div>
    <div class="column is-one-fifth">
      <h3>Movers</h3>
      <ul>
        <li v-for="mover in movers">
          <a :href="binanceLink(mover)"> {{ mover.baseAsset }} / {{ mover.quoteAsset }}</a>
        </li>
      </ul>
    </div>

    <div class="column is-one-fifth">
      <h4>Copy laggers</h4>
      <div class="control" :class="{'is-loading': laggers.length == 0} ">
        <textarea v-model="laggersTView" class="textarea" type="text" :rows="rowsLaggers" readonly>
        </textarea>
      </div>
      <h4 class="h4-bottom">Copy movers</h4>
      <div class="control" :class="{'is-loading': movers.length == 0}">
        <textarea v-model="moversTView" class="textarea" type="text" :rows="rowsMovers" readonly>
        </textarea>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'LaggersMoversTable',
  props: ['laggers', 'movers'],
  computed: {
    laggersTView() {
      return this.laggers.map(l => l['symbol']).join(', ')
    },
    moversTView() {
      return this.movers.map(l => l['symbol']).join(', ')
    },
    rowsLaggers() {
      if (this.laggers.length == 0)
        return 3
      else
        return Math.ceil(this.laggers.length / 4)
    },
    rowsMovers() {
      if (this.movers.length == 0)
        return 3
      else
        return Math.ceil(this.movers.length / 4)
    }
  },
  methods: {
    binanceLink(symbol) {
      return 'https://www.binance.com/en/trade/' + symbol.baseAsset + '_' + symbol.quoteAsset;
    }
  }
}
</script>

